import fieldValidation from "../../../components/fieldValidation";
import saveSubmitFun from "../../../components/saveSubmitFun";

async function handleSubmitFun(event,url) {
    event.preventDefault();
    if (this.state.applicant === "" || this.state.applicant === null) {
        let msg = "Please select Applicant before Save"
        return fieldValidation.call(this, msg, "applicant")
    }
    else if (this.state.locSelected === "" || this.state.locSelected === null) {
        let msg = "Please select Location before Save"
        return fieldValidation.call(this, msg, "loc_desc")
    }
    else if (this.state.receipt_mode === "" || this.state.receipt_mode === null) {
        let msg = "Please select Receipt Mode before Save"
        return fieldValidation.call(this, msg, "receipt_mode")
    }
    else {
        var url = url
        saveSubmitFun.call(this, event, url)
    }
}

export default handleSubmitFun;
