import moment from "moment";
import getDetailsFun from "../../../components/getDetailsFun";

async function getDetailsFunction(form_name) {
    if (this.props.navOrgName === "" && this.props.adminAccess === false) {
        return alert("Please change organisation to view details")
    }
    this.setState({ loading: true });
    const queryParams = window.location.pathname;
    await getDetailsFun.call(this, queryParams, form_name)
    if (this.state.details !== "") {
        this.setState({
            obj_id: this.state.details.id,
            inward_no: this.state.details.inward_no,
            applicant: this.state.details.applicant,
            other_applicant: this.state.details.other_applicant,
            allocate_to: this.state.details.allocate_to,
            receipt_mode: this.state.details.receipt_mode,
            courier_company: this.state.details.courier_company,
            awb_consignment_no: this.state.details.awb_consignment_no,
            description: this.state.details.description,
            depSelected: this.state.details.dep_selected,
            locSelected: this.state.details.loc_selected,
            person_name: this.state.details.person_name,
            status: this.state.details.status,
            loading: false,
        })
        if (this.state.details.receipt_mode === "courier") {
            this.setState({
                courierFieldDisplay: true,
                awbFieldDisplay: true,
                handFieldDisplay: false,
            })
        }
        else if (this.state.details.receipt_mode === "hand") {
            this.setState({
                courierFieldDisplay: false,
                handFieldDisplay: true,
                awbFieldDisplay: false,
            })
        }
        else if (this.state.details.receipt_mode === "hand") {
            this.setState({
                courierFieldDisplay: false,
                awbFieldDisplay: false,
                handFieldDisplay: true,
            })
        }
        else if (this.state.details.receipt_mode === "post") {
            this.setState({
                courierFieldDisplay: false,
                awbFieldDisplay: true,
                handFieldDisplay: false,
            })
        }
        if (this.state.details.other_applicant) {
            this.setState({
                otherApplicantDisplay: true,
            })
        }
    }
}

export default getDetailsFunction;
