/* eslint-disable react/no-direct-mutation-state */
import React, { Component } from "react";
import { Navigate } from "react-router-dom";
import FormName from "../../components/FormNameUpdatePage";
import Wrapper_ClickFun from "../../components/wrapperClickFun";
import ChangeHandler from "../../components/ChangeHandler";
import Loader from "../../components/Loader";
import SubmitButton from "../../components/submitButton";
import ErrorMessage from "../../components/errorMessage";
import { Dropdown } from "semantic-ui-react";
import { connect } from "react-redux";
import drpHandleChangeFun from "./functions/drpHandleChangeFun";
import getDetailsFunction from "./functions/getDetailsFun";
import masterListFun from "./functions/masterListFun";
import handleSubmitFun from "./functions/handleSubmitFun";

const API_form_name = "inwardEntry";
const List_Path = `/${process.env.REACT_APP_NAME}/${API_form_name}`;
const form_name = "Inward Entry";
const form_type = "Master";

const mapStatetoProps = (state) => {
    return {
        adminAccess: state.adminAccess,
        navOrgName: state.navOrgName
    };
};

const StateDic = {
    details: "",
    obj_id: "",
    inward_no: "",
    applicant: "",
    other_applicant: "",
    locList: [],
    loc_desc: '',
    locSelected: '',
    depList: [],
    dep_desc: '',
    depSelected: '',
    allocate_to: '',
    receipt_mode: '',
    courier_company: '',
    awb_consignment_no: '',
    description: '',
    person_name: '',
    err_msg_visible: false,
    error_message: "",
    loading: true,
    redirect: false,
    status: "",
    courierFieldDisplay: false,
    handFieldDisplay: false,
    awbFieldDisplay: false,
    otherApplicantDisplay: false
};

class CreateUpdatePage extends Component {
    constructor(props) {
        super(props);
        this.state = StateDic;
    }

    drpHandleChange = (e) => {
        drpHandleChangeFun.call(this,e)
    };

    getDetails = async () => {
        getDetailsFunction.call(this)
    };

    componentDidMount() {
        this.getDetails();
        this.masterList();
    }

    masterList = async () => {
        masterListFun.call(this)
    }

    handleSubmit = async (event) => {
        var url = `${List_Path}/CreateUpdateAPI/`
        handleSubmitFun.call(this,event,url)
    };

    locChange = (e, value) => {
        this.setState({
            loc_desc: this.state.loc_desc = e.target.innerText,
            locSelected: this.state.locSelected = value
        });
        if (this.state.locSelected === "") {
            const fieldName = "locanisation"
            this.setState({ loading: false })
            return this.requiredElement(fieldName)
        }
    }

    depChange = (e, value) => {
        this.setState({
            dep_desc: this.state.dep_desc = e.target.innerText,
            depSelected: this.state.depSelected = value
        });
        if (this.state.depSelected === "") {
            const fieldName = "department"
            this.setState({ loading: false })
            return this.requiredElement(fieldName)
        }
    }

    render() {
        const { error_message, redirect } = this.state;

        if (redirect) {
            return <Navigate to={List_Path} />;
        }

        return (
            <div className="wrapper">
                <div className="content-wrapper" onClick={Wrapper_ClickFun.bind(this)}>
                    <form id="app-form" onSubmit={this.handleSubmit} autoComplete="off">
                        <FormName form_name_list={form_name} form_name={form_name} form_type={form_type} list_page={List_Path} auditTrailView={"Yes"} recordNo={this.state.inward_no} tableName={"inward_entry"}></FormName>
                        <Loader load={this.state.loading}></Loader>
                        <ErrorMessage error_message={error_message} err_msg_visible={this.state.err_msg_visible}></ErrorMessage>
                        <div className="row">
                            <p className="title_label">General Information</p>
                            <div className="col-lg-2 col-md-2 col-sm-2 did-floating-label-content">
                                <input className="did-floating-input" type="text" placeholder=" " name="inward_no" id="inward_no" value={this.state.inward_no} onChange={ChangeHandler.bind(this)} onClick={ChangeHandler.bind(this)} readOnly={true} disabled={true} />
                                <label className="did-floating-label">Inward No.</label>
                            </div>

                            <div className="col-lg-2 col-md-2 col-sm-2 did-floating-label-content">
                                <select className="did-floating-select" name="applicant" id="applicant" value={this.state.applicant} onChange={this.drpHandleChange} onClick={this.drpHandleChange}>
                                    <option value="">---Select---</option>
                                    <option value="Vendor">Vendor</option>
                                    <option value="Buyer">Buyer</option>
                                    <option value="Agent">Agent</option>
                                    <option value="Others">Others</option>
                                </select>
                                <label className="did-floating-label">Applicant <span className="mandatory_field">*</span></label>
                            </div>

                            <div className="col-lg-3 col-md-3 col-sm-3 did-floating-label-content" style={{ display: this.state.otherApplicantDisplay ? "block" : "none" }}>
                                <input className="did-floating-input" type="text" placeholder=" " name="other_applicant" id="other_applicant" value={this.state.other_applicant} maxLength={50} onChange={ChangeHandler.bind(this)} onClick={ChangeHandler.bind(this)} />
                                <label className="did-floating-label">Other Applicant</label>
                            </div>

                            <div className="col-lg-3 col-md-3 col-sm-3 did-floating-label-content">
                                <Dropdown className="did-floating-input" name="loc_desc" id="loc_desc" value={this.state.locSelected} clearable placeholder={this.state.locList.length > 0 ? 'Select State' : 'Loading...'} fluid search required={true} selection options={this.state.locList} loading={this.state.locList.length > 0 ? false : true} disabled={this.state.locList.length > 0 ? false : true} onChange={(event, data) => { this.locChange(event, data.value) }} />
                                <label className="did-floating-label">Location <span className="mandatory_field">*</span></label>
                                <div className="form-helper helper_hide" id="loc_desc_helper">Required</div>
                            </div>
                            <div className="col-lg-2 col-md-2 col-sm-2 did-floating-label-content">
                                <input className="did-floating-input" type="text" placeholder=" " name="status" id="status" value={this.state.status} readOnly={true} disabled={true} />
                                <label className="did-floating-label">Status</label>
                            </div>
                            <p className="title_label">Allocation Information</p>
                            <div className="col-lg-3 col-md-3 col-sm-3 did-floating-label-content">
                                <Dropdown className="did-floating-input" name="dep_desc" id="dep_desc" value={this.state.depSelected} clearable placeholder={this.state.depList.length > 0 ? 'Select State' : 'Loading...'} fluid search required={true} selection options={this.state.depList} loading={this.state.depList.length > 0 ? false : true} disabled={this.state.depList.length > 0 ? false : true} onChange={(event, data) => { this.depChange(event, data.value) }} />
                                <label className="did-floating-label">Department <span className="mandatory_field">*</span></label>
                                <div className="form-helper helper_hide" id="dep_desc_helper">Required</div>
                            </div>

                            <div className="col-lg-3 col-md-3 col-sm-3 did-floating-label-content">
                                <input className="did-floating-input" type="text" placeholder=" " name="allocate_to" id="allocate_to" required value={this.state.allocate_to} onChange={ChangeHandler.bind(this)} onClick={ChangeHandler.bind(this)} />
                                <label className="did-floating-label">Allocate To <span className="mandatory_field">*</span></label>
                                <div className="form-helper helper_hide" id="allocate_to_helper">
                                    Required
                                </div>
                            </div>

                            <p className="title_label">Inward Details</p>
                            <div className="col-lg-2 col-md-2 col-sm-2 did-floating-label-content">
                                <select className="did-floating-select" name="receipt_mode" id="receipt_mode" value={this.state.receipt_mode} onChange={this.drpHandleChange} onClick={this.drpHandleChange} onFocus={this.drpHandleChange}>
                                    <option value="">---Select---</option>
                                    <option value="courier">Courier</option>
                                    <option value="hand">Hand</option>
                                    <option value="post">Post</option>
                                </select>
                                <label className="did-floating-label">Receipt Mode <span className="mandatory_field">*</span></label>
                            </div>

                            <div className="col-lg-3 col-md-3 col-sm-3 did-floating-label-content" style={{ display: this.state.courierFieldDisplay ? "flex" : "none" }}>
                                <input className="did-floating-input" type="text" placeholder=" " name="courier_company" id="courier_company" required={this.state.courierFieldDisplay} value={this.state.courier_company} onChange={ChangeHandler.bind(this)} onClick={ChangeHandler.bind(this)} />
                                <label className="did-floating-label">Courier Company <span className="mandatory_field">*</span></label>
                                <div className="form-helper helper_hide" id="courier_company_helper">
                                    Required
                                </div>
                            </div>

                            <div className="col-lg-3 col-md-3 col-sm-3 did-floating-label-content" style={{ display: this.state.handFieldDisplay ? "flex" : "none" }}>
                                <input className="did-floating-input" type="text" placeholder=" " name="person_name" id="person_name" required={this.state.handFieldDisplay} value={this.state.person_name} onChange={ChangeHandler.bind(this)} onClick={ChangeHandler.bind(this)} />
                                <label className="did-floating-label">Person Name <span className="mandatory_field">*</span></label>
                                <div className="form-helper helper_hide" id="person_name_helper">
                                    Required
                                </div>
                            </div>

                            <div className="col-lg-3 col-md-3 col-sm-3 did-floating-label-content" style={{ display: this.state.awbFieldDisplay ? "flex" : "none" }}>
                                <input className="did-floating-input" type="text" placeholder=" " name="awb_consignment_no" id="awb_consignment_no" required={this.state.awbFieldDisplay} value={this.state.awb_consignment_no} onChange={ChangeHandler.bind(this)} onClick={ChangeHandler.bind(this)} />
                                <label className="did-floating-label">AWB/Consignment Number <span className="mandatory_field">*</span></label>
                                <div className="form-helper helper_hide" id="awb_consignment_no_helper">
                                    Required
                                </div>
                            </div>
                            <div className="col-lg-12 col-md-12 col-sm-12 did-floating-label-content">
                                <div className="col-lg-6 col-md-6 col-sm-6 did-floating-label-content">
                                    <textarea className="did-floating-input" type="text" placeholder=" " cols="80" rows="4" name="description" id="description" value={this.state.description} onChange={ChangeHandler.bind(this)} onClick={ChangeHandler.bind(this)} />
                                    <label className="did-floating-label">Description</label>
                                </div>
                            </div>

                            <SubmitButton form_type={form_type} list_page={List_Path}></SubmitButton>
                        </div>
                    </form>
                </div>
            </div>
        );
    }
}

export default connect(mapStatetoProps)(CreateUpdatePage);
